<template>
  <span :endTime="endTime">
    <slot>
      {{content}}
    </slot>
  </span>
</template>
<script>
export default {
  data() {
    return {
      content: "",
      timer: ""
    };
  },
  props: {
    endTime: {
      default: ""
    },
    flag: ""
  },
  mounted() {
    this.countdowm(this.endTime, this.flag);
  },
  methods: {
    countdowm(val, flag) {
      let self = this;
      var abc = function() {
        var utc = (val - new Date().getTime()) / 1000;
        if (utc <= 0) {
          if (flag) {
            self.content = "00" + "分" + "00" + "秒";
          } else {
            self.content =
              "剩余: " + "00" + "天" + "00" + "时" + "00" + "分" + "00" + "秒";
          }
          self.$emit("update:endTime", self.content);
          clearInterval(self.timer);
        } else {
          var tian = Math.floor(utc / (24 * 60 * 60));
          var shi = Math.floor((utc / 60 / 60) % 24);
          var fen = Math.floor((utc / 60) % 60);
          var miao = Math.floor(utc % 60);
          if (tian < 10) {
            tian = "0" + tian;
          }
          if (shi < 10) {
            shi = "0" + shi;
          }
          if (fen < 10) {
            fen = "0" + fen;
          }
          if (miao < 10) {
            miao = "0" + miao;
          }
          if (flag) {
            self.content = fen + "分" + miao + "秒";
          } else {
            self.content =
              "剩余: " + tian + "天" + shi + "时" + fen + "分" + miao + "秒";
          }
          self.$emit("update:endTime", self.content);
        }
      };
      abc();
      self.timer = setInterval(function() {
        abc();
      }, 1000);
    }
  },
  watch: {
    content: function(val, oldVal) {
      if (val == "00分00秒") {
        this.$emit("update:endTime", val);
      }
    }
  }
};
</script>


